import React from 'react';
import { Navbar, Nav, NavDropdown } from 'react-bootstrap';
import '../../Style/ItemNavbar.css'
import {Link} from 'react-router-dom'
import { useCartContext } from '../CartContext/cartContext'
import {Card} from 'react-bootstrap'
import '../../Style/Item.css'


export const ItemNavbar = () => {
    const {getUnits} = useCartContext()

    return(
        <React.Fragment>
            <Navbar expand="lg" className='menuMargin'>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav" className="menu">
                    <Link to='/'>
                        <Card.Img variant="top" src={'https://win98icons.alexmeub.com/icons/png/computer_2_cool-4.png'} style={{ width: '50px', height: '50px'}}/>
                        <Nav className='menu-item'>Home</Nav>
                    </Link>
                    <Link>
                        <Card.Img variant="top" src={'https://win98icons.alexmeub.com/icons/png/search_directory-5.png'} style={{ width: '50px', height: '50px'}}/>
                        <NavDropdown title="Productos" id="basic-nav-dropdown">
                            <Link to={'/category/acciones'}>
                                <NavDropdown.Item href="#action/3.1">Acciones</NavDropdown.Item>
                            </Link>
                            <Link to={'/category/criptomoneda'}>
                                <NavDropdown.Item href="#action/3.2">Criptomonedas</NavDropdown.Item>
                            </Link>
                        </NavDropdown>
                    </Link>
                    <Link>
                        <Card.Img variant="top" src={'https://win98icons.alexmeub.com/icons/png/notepad-5.png'} style={{ width: '50px', height: '50px'}}/>
                        <Nav className='menu-item'>Contacto</Nav>
                    </Link>
                    <Link to={'/cart'}>
                        <Card.Img variant="top" src={'https://win98icons.alexmeub.com/icons/png/recycle_bin_full-4.png'} style={{ width: '50px', height: '50px'}}/>
                        <Nav className='menu-item'>Cart <i>{getUnits()}</i></Nav>
                    </Link>
                </Navbar.Collapse>
            </Navbar>
            <div className="container-fluid">
            <div className="lower-bar">
                <div className="windows-btn">
                    <img src="data:image/png;base64," width="26" height="20" alt="Start"></img>
                    <span>Start</span>
                </div>
                <div className="open-doc">
                    <img src="data:image/png;base64," alt="" width="31" height="25"/>
                    <span>Productos</span>
                </div>
                <div className="slot"></div>
                <div className="timer">
                    <img src="data:image/png;base64," width="17" height="17" alt="Sound"></img>
                </div>
            </div>
        </div>
        </React.Fragment>
    )
} 
export default ItemNavbar