import React, { useState, useEffect } from 'react'
import Item from './Item'
import '../../Style/Item.css'
import { getFirestore } from '../firebase'
import { useParams } from 'react-router'
import Loading from '../Loading/Loading'

const ItemListContainer = () => {
    const [  arrayItems, setArrayItems ] = useState([])
    const {id} = useParams();
    useEffect(()=>{
        const db = getFirestore();
        let itemsFilter = '';
        if (id) {
            itemsFilter = db.collection("items").where("categoryId", "==", id) //PARA FILTRAR LOS ITEMS QUE TRAIGO
        }else {
            itemsFilter = db.collection("items")
        }
        itemsFilter.get()
            .then((querySnapShot)=>{
                querySnapShot.size === 0 ? console.log('No hay items') : console.log(`Hay ${querySnapShot.size} items`);
                const documentos = querySnapShot.docs.map((doc)=>{
                    return {
                        id: doc.id,
                        ...doc.data()
                    }
                });
                setArrayItems(documentos)
            })
            .catch((err)=>console.log('Hubo un error', err))
            .finally(()=>console.log('Cargó correctamente'))
    }, [id])

    useEffect(() => {
        arrayItems.length && console.log(arrayItems);
    }, [arrayItems])
    return(
        <React.Fragment>
            <div className="container">
                <div className="desktop">
                    <div className="title">
                        <h1>Productos</h1>
                    </div>
                    <ul className="tools">
                        <li><u>F</u>ile</li>
                        <li><u>E</u>dit</li>
                        <li><u>V</u>iew</li>
                        <li><u>H</u>elp</li>
                    </ul>
                    <div className="desktopContainer">
                        <div className='cards'>
                            { arrayItems.length > 0 ?
                                arrayItems.map((dato)=>
                                    <div key={dato.id}>
                                        <Item img={dato.img} title={dato.title} price={dato.price} stock={dato.stock} id={dato.id} categoryId={dato.categoryId}/>
                                    </div>
                                ) : <Loading/>
                            }
                        </div>
                    </div>
                </div>
            </div>
            <div className="container-fluid">
                <div className="lower-bar">
                    <div className="windows-btn">
                        <img src="data:image/png;base64," width="26" height="20" alt="Start"></img>
                        <span>Start</span>
                    </div>
                    <div className="open-doc">
                        <img src="data:image/png;base64," alt="" width="31" height="25"/>
                        <span>Productos</span>
                    </div>
                    <div className="slot"></div>
                    <div className="timer">
                        <img src="data:image/png;base64," width="17" height="17" alt="Sound"></img>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}
export default ItemListContainer